/*  <general>  */
html {
  font-size: 62.5%;
  cursor: default;
}
-webkit-text-size-adjust 100%,
-ms-text-size-adjust 100% {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #3f4040;
  font: normal 1.6rem/1.5 'Rubik';
  background-color: #fff;
}
:-moz-placeholder {
  opacity: 1;
  color: currentColor;
}
:-ms-input-placeholder {
  opacity: 1;
  color: currentColor;
}
a {
  color: #0098dc;
  font: inherit;
  text-decoration: none;
  transition: all 0.2s;
}
a:hover {
  text-decoration: underline;
}
img {
  max-width: 100%;
}
.main {
  flex: 1;
}
/*  </general>  */
/*  <header>  */
.header {
  flex-shrink: 0;
}
/*  </header>  */
/*  <nav>  */
.nav {
  flex-shrink: 0;
}
/*  </nav>  */
/*  <footer>  */
.footer {
  flex-shrink: 0;
}
